import { useEffect, useState } from "react"
import { useTranslation } from "react-i18next"
import {data_blog} from "../../data/datas"
import BlogItem from "../templates/blog-item"

const Blog = ()=>{
	const { t } = useTranslation();



	// 1. Cho ln chy u tin.
	const [data, setData] = useState([]);

	useEffect(() => {
		fetchDataFromJSON(4); // ln load u tin l 4
	}, []); // Mng rng s m bo hm useEffect ch c gi mt ln khi component c ti ln u

	const fetchDataFromJSON = async (param) => {
		console.log("Fetching data from json param:", param);
		try {
			const response = await fetch(data_blog.fetch+'/posts?per_page='+param);
			const jsonData = await response.json();
			setData(jsonData);
			console.log("Fetched data from json:", jsonData);
		} catch (error) {
	  		console.log('Error fetching data:', error);
		}
	};
	// const handleButtonClick = (param) => {
	// 	console.log('Button clicked with parameter:', param);
	// };




	// 2. Cho s kin button click
	const [fetchDataParam, setFetchDataParam] = useState('');

	// Khi bin fetchDataParam thay i, ta s gi hm fetchDataFromJSON  ti d liu
	useEffect(() => {
		if (fetchDataParam) {
		  	fetchDataFromJSON(fetchDataParam);
		  	setFetchDataParam('');
		}
	}, [fetchDataParam]);

	const handleButtonClick = (param) => {
		console.log('Button clicked with parameter:', param);
		setFetchDataParam(param);
	};

	

	return(
		<>
			<div className="w3-content w3-padding-32 content">
				{/*<EffectDemo/>*/}
				<h2>Blog <span className="w3-medium">(Chat GPT Supported)</span></h2>
				<hr/>
				<div className="filter">
					<button 
						onClick={() => handleButtonClick(4)} 
						className="w3-button w3-border w3-small mrr8">
							4 {t("bi vit")}
                    </button>
                    <button 
						onClick={() => handleButtonClick(6)} 
						className="w3-button w3-border w3-small mrr8">
							6 {t("bi vit")}
                    </button>
                    <button 
						onClick={() => handleButtonClick(8)} 
						className="w3-button w3-border w3-small mrr8">
							8 {t("bi vit")}
                    </button>
				</div>
				<div className="posts col-container row-fix-margin">
					{data.map((post, index) => (
						<BlogItem key={index} post={post} />
			     	))}
						
				</div>
				<div className="more w3-center w3-margin-top">
					<a 
						target="_blank"
						rel="noreferrer"
						href={data_blog.url}
						className="w3-button w3-border">
						{t("Xem thm")}
					</a>
				</div>
			</div>
		</>
	)
}
export default Blog