import { useTranslation } from "react-i18next";
import { Link } from "react-router-dom"
import { projectsDataItems} from '../../data/projects'

const ProjectsCv = () => {
    let project_web = [];
    projectsDataItems.map((item,key)=>(
        (item.type==='web') ? project_web.push(item) : ''
    ))
    const { t } = useTranslation();
    return ( 
        <>
        <div id="projects">
            <h2 className="w3-padding-16">{t("D n gn y")}</h2>
            <div className="">
                <ol className="w3-ol">
                    {

                        project_web.map((item,key)=>(
                            (item.type==='web' && key <=5) ? 
                                <li key={key}>
                                    <a className="link" target="_blank" rel="noreferrer" href={item.link}>{item.title}</a>
                                </li> : 
                                ''
                        ))
                    }


                    <li><a className="link" target="_blank" rel="noreferrer" href="https://wordpress.org/plugins/administrator-z/">{t("Plugin wordpress Administrator Z")}</a></li>
                    <li><a className="link" target="_blank" rel="noreferrer" href="https://wordpress.org/plugins/auto-product-after-upload-image/">{t("Plugin wordpress t to sn phm khi ti hnh nh")}</a></li>
                    <li><Link className="link" to="/projects">{t("Xem thm")}...</Link></li>
                </ol>
            </div>
        </div>
        </>
    )
}
export default ProjectsCv